import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import 'rxjs/Rx';
import { Task } from '../task';

@Injectable()
export class TaskService {
  domain: string = "http://localhost:3000";
  constructor(private http: HttpClient) { }

  getTask(){
    return this.http.get<Task[]>(`${this.domain}/api/tasks`).map(res=>res);
  }

  addTask(newTask: Task){
    return this.http.post<Task>(`${this.domain}/api/tasks`,newTask).map(res=>res);
  }

  updateTask(newTask: Task){
    return this.http.put(`${this.domain}/api/tasks/${newTask._id}`,newTask).map(res=>res);
  }

  deleteTask(id){
    return this.http.delete<Task>(`${this.domain}/api/tasks/${id}`).map(res=>res);
  }

}




